h4,\nh5,\nh6,\n.type__headline__hero,\n.type__headline__xl,\n.type__headline__l,\n.type__headline__m,\n.type__headline__s,\n.type__headline__xs,\n.type__headline__xxs,\n.type__subline__xxxs {\n  @include type__headline__defaults();\n}\n\n.type__subline__hero,\n.type__subline__xl,\n.type__subline__l,\n.type__subline__m,\n.type__subline__s,\n.type__subline__xs,\n.type__subline__xxs,\n.type__subline__xxxs {\n  color: $color__subline;\n  font-weight: 400;\n  margin-bottom: 0.4em;\n}\n// TODO: make this a mixin\n.background--blurple,\n.background--warden,\n.background--project,\n.background--low-contrast {\n  color: $color__white;\n\n  h1,\n  h2,\n  h3,\n  h4,\n  h5,\n  h6,\n  .type__headline__hero,\n  .type__headline__xl,\n  .type__headline__l,\n  .type__headline__m,\n  .type__headline__s,\n  .type__headline__xs,\n  .type__headline__xxs {\n    color: $color__white;\n  }\n\n  .type__subline__hero,\n  .type__subline__xl,\n  .type__subline__l,\n  .type__subline__m,\n  .type__subline__s,\n  .type__subline__xs,\n  .type__subline__xxs {\n    color: rgba($color__white, 0.7);\n  }\n  p,\n  span {\n    color: $color__white;\n  }\n}\n\n// [Tip] When using headlines, use the `type__` classes instead of the `h` tags. Use the correct `h` tag for the position you're in, and assign it a new class.\n// Headline xl\n@mixin type__headline__xl {\n  @include type__headline__defaults();\n  font-size: $headline-font-size__l;\n  line-height: 115%;\n  @include breakpoint(\"m\") {\n    font-size: $headline-font-size__xl;\n  }\n}\n.type__headline__xl,\nh1 {\n  @include type__headline__xl;\n}\n\n// Headline l\n@mixin type__headline__l {\n  @include type__headline__defaults();\n  font-size: $headline-font-size__m;\n  line-height: 130%;\n\n  @include breakpoint(\"m\") {\n    font-size: $headline-font-size__l;\n  }\n}\n.type__headline__l,\nh2 {\n  @include type__headline__l;\n}\n\n// Headline m\n@mixin type__headline__m {\n  @include type__headline__defaults();\n  font-size: $headline-font-size__s;\n  line-height: 120%;\n\n  @include breakpoint(\"m\") {\n    font-size: $headline-font-size__m;\n  }\n}\n.type__headline__m,\nh3 {\n  @include type__headline__m;\n}\n\n// Headline s\n@mixin type__headline__s {\n  @include type__headline__defaults();\n  font-size: $headline-font-size__xs;\n  line-height: 120%;\n\n  @include breakpoint(\"m\") {\n    font-size: $headline-font-size__s;\n  }\n}\n.type__headline__s,\nh4 {\n  @include type__headline__s;\n}\n\n// Headline xs\n@mixin type__headline__xs {\n  @include type__headline__defaults();\n  font-size: $headline-font-size__xxs;\n  line-height: 120%;\n\n  @include breakpoint(\"m\") {\n    font-size: $headline-font-size__xs;\n  }\n}\n.type__headline__xs,\nh5 {\n  @include type__headline__xs;\n}\n\n// Headline xxs\n@mixin type__headline__xxs {\n  @include type__headline__defaults();\n  font-size: $headline-font-size__xxxs;\n  line-height: 120%;\n\n  @include breakpoint(\"m\") {\n    font-size: $headline-font-size__xxs;\n  }\n}\n.type__headline__xxs,\nh6 {\n  @include type__headline__xxs;\n}\n\n// Headline xxxs\n@mixin type__headline__xxxs {\n  @include type__headline__defaults();\n  font-size: $headline-font-size__xxxs;\n  line-height: 120%;\n\n  @include breakpoint(\"m\") {\n    font-size: $headline-font-size__xxxs;\n  }\n}\n.type__headline__xxxs {\n  @include type__headline__xxxs;\n}\n\n// Special Headlines\n// ------------------------------------------------\n\n.type__headline__page-title {\n  font-size: $headline-font-size__l;\n  line-height: 115%;\n  margin-bottom: $spacing__l;\n  @include breakpoint(\"m\") {\n    font-size: $headline-font-size__xl;\n    margin-bottom: $spacing__xl;\n  }\n}\n.type__subline__page-title {\n  margin-bottom: $spacing__l;\n  margin-top: -$spacing__s; // TODO: don't use negative margin\n  font-size: $headline-font-size__xxxs;\n  font-weight: 500;\n  line-height: 135%;\n\n  @include breakpoint(\"m\") {\n    font-size: $headline-font-size__xxs;\n    margin-bottom: $spacing__xl;\n    line-height: 115%;\n  }\n}\n\n// Sublines\n// ------------------------------------------------\n\n.type__subline__s {\n  margin-bottom: 0.3em;\n  font-size: $headline-font-size__xxxs;\n  font-family: $font__default;\n  font-weight: 500;\n  font-style: normal;\n  line-height: 120%;\n  text-decoration: none;\n  text-transform: none;\n  @include breakpoint(\"m\") {\n    font-size: $headline-font-size__xxxs;\n  }\n}\n\n.type__subline__m {\n  font-size: $headline-font-size__xxxs;\n  font-family: $font__default;\n  font-weight: 500;\n  font-style: normal;\n  line-height: 120%;\n  text-decoration: none;\n  text-transform: none;\n  margin-bottom: 0.3em;\n  @include breakpoint(\"m\") {\n    font-size: $headline-font-size__xxs;\n  }\n}\n\n// Heroes\n// ------------------------------------------------\n\n.type__headline__hero {\n  font-size: $headline-font-size__xl;\n  font-family: $font__default;\n  font-weight: 700;\n  font-style: normal;\n  line-height: 115%;\n  text-decoration: none;\n  text-transform: none;\n  margin-bottom: 0.3em;\n  @include breakpoint(\"m\") {\n    font-size: $headline-font-size__xxl;\n  }\n}\n\n.type__headline__landing-hero {\n  margin-top: $spacing__l;\n  margin-bottom: 0.3em;\n  text-align: center;\n  font-size: $headline-font-size__l;\n\n  @include breakpoint(\"xxs\") {\n    font-size: $headline-font-size__ll;\n  }\n\n  @include breakpoint(\"m\") {\n    margin-top: $spacing__xxl;\n    font-size: $headline-font-size__xxl;\n  }\n}\n\n.type__subline__landing-hero {\n  max-width: 500px;\n  margin: 0 auto;\n  text-align: center;\n  line-height: 1.5;\n  font-weight: 400;\n  font-size: 1.1rem;\n\n  @include breakpoint(\"m\") {\n    max-width: 700px;\n    font-size: 1.3rem;\n  }\n}\n// Code\n// ------------------------------------------------\n\npre {\n  width: calc(96vw - 2 * #{$spacing__xl});\n  margin: $spacing__xl 0;\n  padding: $spacing__s $spacing__m;\n  overflow-x: auto;\n\n  background: $color__n-90 !important;\n\n  font-family: $font__code;\n  font-size: $font-size__copy__code;\n  line-height: 1.45;\n\n  border-radius: $border-radius__m;\n\n  @include breakpoint(\"m\") {\n    width: 100%;\n  }\n\n  code {\n    background-color: $color__n-90 !important;\n  }\n\n  &::-webkit-scrollbar-thumb {\n    height: 10px;\n    background-color: $color__blurple-70;\n  }\n\n  &::-webkit-scrollbar {\n    height: 10px;\n  }\n\n  &::-webkit-scrollbar-track {\n    border: none;\n    background-color: $color__n-90;\n  }\n}\n\n// Summary / Details\n// ------------------------------------------------\n\nsummary,\n.summary {\n  display: list-item;\n\n  &::marker {\n    color: $color__n-60;\n  }\n\n  &--right-marker {\n    list-style-position: inside;\n    direction: rtl; // TODO not this (this is how I'm making the arrow show up on the right)\n    text-align: left;\n  }\n}\n\n// Type classes\n// ------------------------------------------------\n\n// Copy\n// -----------------\n// This is the default type style for the site. It is used for most of the copy.\n// Adding a class of `type__copy` to a parent element will apply these styles.\n\n.type__copy {\n  font-size: $font-size__copy;\n  font-family: $font__default;\n  font-weight: 500;\n  line-height: 150%;\n}\n.type__copy-p,\n.type__copy p {\n  margin-bottom: 1.5em;\n}\n.type__copy__bold,\n.type__copy strong {\n  font-weight: 700;\n}\n.type__copy__inline-link,\n.type__copy a {\n  font-weight: 700;\n  text-decoration: underline;\n}\n.type__copy__inline-code,\n.type__copy p code,\n.type__copy h1 code,\n.type__copy h2 code,\n.type__copy h3 code,\n.type__copy h4 code,\n.type__copy h5 code,\n.type__copy h6 code,\n.type__copy li code,\n.type__copy dt code,\n.type__copy dd code,\n.type__copy td code,\n.type__copy th code {\n  padding: 0.1em 0.3em;\n  border-radius: 0.2em;\n\n  background-color: $color__n-80;\n  color: $color__n-10;\n\n  font-family: $font__code;\n  font-style: normal;\n  font-weight: 400;\n  font-size: $font-size__copy__code;\n  line-height: inherit;\n}\n.type__copy {\n  h1,\n  h